<template>
  <div class="loginPage">
    <el-form :model="loginForm" :rules="loginFormRules" ref="loginFormRef" class="loginForm">

      <h3 class="formTitle">科王飙医院 门诊服务</h3>

      <el-form-item prop="username">
        <el-input v-model="loginForm.username" type="text" size="large" autocomplete="off"
                  placeholder="用户名 / 手机号 / 身份证号" :prefix-icon="User"/>
      </el-form-item>

      <el-form-item prop="password">
        <el-input v-model="loginForm.password" type="password" size="large" autocomplete="off" placeholder="密码"
                  :prefix-icon="Lock" @keyup.enter="handleLogin" show-password/>
      </el-form-item>

      <el-form-item prop="securityCode">
        <el-input v-model="loginForm.securityCode" type="text" size="large" autocomplete="off" placeholder="验证码"
                  :prefix-icon="CircleCheck" @keyup.enter="handleLogin" style="width: 67%;"/>
        <div class="securityCodeWrapper">
          <img :src="securityCodeImgUrl" @click="getSecurityCode" alt="刷新验证码"/>
        </div>
      </el-form-item>

      <el-form-item>
        <el-checkbox label="记住密码" v-model="loginForm.rememberMe"/>
      </el-form-item>

      <el-form-item style="width: 100%;">
        <el-button type="primary" size="small" @click="handleRegister"
                   style="width: auto; margin: auto; letter-spacing: 1px;" text>&gt;&gt;注
          册&lt;&lt;
        </el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" size="large" @click="handleLogin" style="width: 100%;">登 录</el-button>
      </el-form-item>

    </el-form>
    <Footer class="loginPageFooter"/>
  </div>
  <RegisterDialog v-model:register-dialog-visible="registerDialogVisible"/>
</template>


<script setup>
import {reactive, ref} from 'vue'
import AxiosUtil from "@/utils/AxiosUtil";
import Footer from "@/components/layout/Footer";
import Cookies from 'js-cookie';
import {ElMessage} from "element-plus";
import {User, Lock, CircleCheck} from '@element-plus/icons-vue'
import {encrypt, decrypt} from "@/utils/JSEncryptUtil";
import qs from "qs";
import store from '@/store'
import router from "@/router";
import RegisterDialog from "@/views/RegisterDialog";

// 表单数据对象
// do not use same name with ref
const loginForm = reactive({
  username: '',
  password: '',
  securityCode: '',
  rememberMe: false, //是否记住密码
  uuid: '', //后台传来的验证码字符串值
});

// 验证码图片地址
let securityCodeImgUrl = ref('');

// 表单验证规则
const loginFormRules = reactive({
  username: [{required: true, message: '请输入您的账号', trigger: 'blur',},],
  password: [{required: true, message: '请输入您的密码', trigger: 'blur',},],
  securityCode: [{required: true, message: '请输入验证码', trigger: 'change',},],
});

// 刷新验证码
const getSecurityCode = async () => {
  let res = await AxiosUtil.get('/captcha');
  console.log('captcha res= ', res);
  loginForm.uuid = res.data.uuid;
  loginForm.securityCode = '';
  securityCodeImgUrl.value = res.data.base64Img;
}

// 定义表单引用
const loginFormRef = ref(null);

// 定义“注册”对话框的显示状态
const registerDialogVisible = ref(false);

// 处理登录按钮事件
const handleLogin = () => {
  console.log('login!');
  console.log('loginFormRef.value= ', loginFormRef.value);

  // 表单验证
  loginFormRef.value.validate(async (valid) => {
    console.log('valid= ', valid);
    // 表单验证通过
    if (valid) {
      console.log('表单验证通过');
      // 如勾选了记住密码，则将登录信息保存到Cookie，否则在Cookie中移除
      if (loginForm.rememberMe) {
        console.log('记住密码');
        Cookies.set('username', loginForm.username, {expires: 7});
        Cookies.set('password', encrypt(loginForm.password, {expires: 7}));
        Cookies.set('rememberMe', loginForm.rememberMe, {expires: 7});
      } else {
        console.log('不记住密码');
        Cookies.remove('username');
        Cookies.remove('password');
        Cookies.remove('rememberMe');
      }

      console.log('stringify loginForm=', qs.stringify(loginForm));
      try {
        let res = await AxiosUtil.post('login?' + qs.stringify(loginForm));
        if (res.data.code == 200) {
          console.log('res.data= ', res.data);
          const token = res.data.authorization;
          const menuList = res.data.menuList;
          const perms = res.data.perms;
          const currentUser = res.data.currentUser;

          store.commit('SET_TOKEN', token);
          store.commit('SET_MENULIST', menuList);
          store.commit('SET_PERMS', perms);
          store.commit('SET_USERINFO', currentUser);

          router.replace({path: '/'}); // 导航到首页
          ElMessage({
            message: '登录成功.',
            type: 'success',
          });
        } else {
          ElMessage({
            showClose: true,
            message: res.data.msg,
            type: 'error',
          });
          await getSecurityCode();
        }
      } catch (e) {
        console.log('login e= ', e);
        ElMessage({
          showClose: true,
          message: '哎呀呀~出错了嚯！\n' + e,
          type: 'error',
        });
        await getSecurityCode();
      }
    } else {
      console.log('表单验证不通过');
      ElMessage({
        message: '请将信息填写完整',
        type: 'warning',
      });
    }
  });
}

// 处理注册按钮事件
const handleRegister = () => {
  console.log('register!');
  registerDialogVisible.value = true;
}

// 从Cookie中加载登录信息
const getLoginInfo = () => {
  const username = Cookies.get('username');
  const password = Cookies.get('password');
  const rememberMe = Cookies.get('rememberMe');
  loginForm.username = username == undefined ? loginForm.username : username;
  loginForm.password = password == undefined ? loginForm.password : decrypt(password);
  loginForm.rememberMe = rememberMe == undefined ? loginForm.rememberMe : rememberMe;
}

// 加载一次验证码和登录信息
getSecurityCode();
getLoginInfo();

</script>


<script>
export default {
  name: "Login",
  // created() {
  //   console.log(this.$router);
  // }
}
</script>

<style lang="scss" scoped>
//登录页整页
.loginPage {
  display: flex;
  position: absolute; /*填充整页*/
  width: 100%;
  height: 100%;
  align-items: center; /*align-items 属性可以使元素在交叉轴方向对齐*/
  justify-content: center; /*justify-content属性用来使元素在主轴方向上对齐*/
  background-image: url("../assets/image/login-background.jpg");
  background-size: cover; /*缩放背景图片以完全覆盖背景区*/
}

//登录表单
.loginForm {
  /*box-sizing: border-box;*/
  width: 400px;
  /*height: 300px;*/
  padding: 25px 25px 5px 25px;
  background: #ffffff;
  border-radius: 5px;

  .formTitle {
    margin: 0 auto 30px auto;
    text-align: center;
    color: #707070;
    font-size: medium;
    font-weight: normal;
  }

  .el-input {
    height: 40px;
  }
}

.securityCodeWrapper {
  width: 33%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: flex-end; /*justify-content属性用来使元素在主轴方向上对齐*/

  img {
    cursor: pointer;
    //width: 1000px;
    height: 40px;
    right: 0;
    border-radius: var(--el-border-radius-base);
  }
}

.loginPageFooter {
  position: fixed;
  bottom: 0;
  color: #ffffff;
  background: transparent; /*仅供测试*/
}

</style>